import React,{Component} from 'react';

const url="https://zomatojulyapi.herokuapp.com/menuItem";
const PostUrl = "https://zomatojulyapi.herokuapp.com/placeOrder";
class PlaceOrder extends Component
{
    constructor(props){
    super(props)
    this.state={
    details:'',
    amount:0,
    hotel_name:this.props.match.params.restName,
    name:'',
    email:'',
    phone:'',
    address:'',
    status:'Pending'
    }
    }
    handleChange = (event) => {
        this.setState({[event.target.name]:event.target.value})
    }
    handleSubmit = () => {
        fetch(PostUrl,
            {
                method:'POST',
                headers:{
                    'accept':'application/json',
                    'content-type':'application/json'
                },
                body:JSON.stringify(this.state)
            }
        )
        .then(this.props.history.push('/viewOrder'))
    }
    renderItems =  (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <div className="items" key={item.menu_id}>
                        <img src={item.menu_image} alt={item.menu_name}/>
                        <h3>{item.menu_name}</h3>
                        <h4>Rs {item.menu_price}</h4>
                    </div>
                    
                )
            })
        }else{
            return(
                <img src="/images/loader.gif" alt="loader"/>
            )
        }
    }
    render(){
        return(
        <div className="container">
            <div className="panel panel-primary">
                <div className=" panel-heading">
                    <h3>
                        you order from {this.props.match.params.restName}is below:
                    </h3>
                </div>
                <div className="panel-body">
                <form method="POST" action="http://localhost:4000/paynow">
                        <div className="row">
                            
                            <div className="col-md-12">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Name</label>
                                        <input className="form-control" name="name" value={this.state.name}
                                        onChange={this.handleChange}/>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>EmailId</label>
                                        <input className="form-control" name="email" value={this.state.email}
                                        onChange={this.handleChange}/>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Phone</label>
                                        <input className="form-control" name="phone" value={this.state.phone}
                                        onChange={this.handleChange}/>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Address</label>
                                        <input className="form-control" name="address" value={this.state.address}
                                        onChange={this.handleChange}/>
                                    </div>
                                </div>
                                
                            </div>
                            <input type="hidden" name="amount" value={this.state.amount}/>
                            
                        </div>
                        {this.renderItems(this.state.details)}
                        <div className="row">
                            <div className="col-md-12">
                                <h2>Total Cost is Rs.{this.state.amount}</h2>
                            </div>
                        </div>
                        <button className="btn btn-success" onClick={this.handleSubmit} 
                        type="submit">
                                    Checkout
                        </button>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
componentDidMount() {
    var menuItem =sessionStorage.getItem('menu');
    var orderId=[]
    menuItem.split(',').map((item)=>{
       orderId.push(parseInt(item))
       return 'ok'
   })
    fetch(url,{
        method:'POST',
        headers:{
            'accept':'application/json',
            'Content-Type':'application/json'

        },
        body:JSON.stringify(orderId)
    })
    
    .then((res)=>res.json())
    .then((data)=>
    {
        var Totalprice = 0;
        data.map((item) => {
            Totalprice = Totalprice+parseInt(item.menu_price)
            return 'ok'
    })
    this.setState({details:data,amount:Totalprice})
    })
}
}
export default PlaceOrder;