import React, { Component } from 'react';
import axios from 'axios';
import ListingDisplay from "./listingDisplay";
import CuisineFilter from "../filters/cuisineFilter";
import CostFilter from "../filters/costFilter";
import Header from "../Header";


const url="https://zomatojulyapi.herokuapp.com/restaurant?mealtypeid=2";

class Listing extends Component{
    constructor(props){
        super()
        this.state={
            restaurantList:''
        }
    }
    setDataAsPerFilter=(sortedData)=>{
        this.setState({restaurantList:sortedData})
    }
    render(){
        return(
            <div className="row">
                       <Header/>
                <div id="mainListing">
                    <h1>BreakFast Places in Mumbai</h1>
                    <div id="filter">
                    <CuisineFilter restPerCusinie={(data) => {this.setDataAsPerFilter(data)}}/>
                    <hr/>
                    <CostFilter restPerCost={(data) => {this.setDataAsPerFilter(data)}}/>
                    </div>
                    <div id="content">
                    <ListingDisplay listData={this.state.restaurantList}/>
                    </div>
                </div>
            </div>

        )
    }
    // callApi
    componentDidMount(){
        let mealId=this.props.match.params.mealId;
        sessionStorage.setItem('mealId',mealId)
        axios.get(`${url}${mealId}`)
        .then((res)=>{this.setState({ restaurantList:res.data})})
    }


}

export default Listing